import { html, css, LitElement } from "lit-element"
import realize from "../realize"

const styles = css`
  :host {
    display: block;
  }

  .graph-node__pattern {
    min-height: 1.8em;
    text-align: center;
    background-color: greenyellow;
  }
`

class GraphContentPattern extends LitElement {
  render() {
    return html`
      <div class="graph-node__pattern" @contextmenu=${this.openConetextMenu}>
        ${this.patternData ? this.patternData.name : "()"}
      </div>
    `
  }

  static get styles() {
    return styles
  }

  get patternData() {
    return this.__pattern
  }
  set patternData(value) {
    this.__pattern = value
    this.requestUpdate()
  }

  openConetextMenu(e) {
    e.stopPropagation()
    e.preventDefault()

    const template = html`
      <graph-contextmenu posx=${e.x} posy=${e.y}>
        <graph-contextmenu-item
          .operation=${() => this.parentElement.destroy()}
        >
          Remove
        </graph-contextmenu-item>
      </graph-contextmenu>
    `

    document.body.append(...realize(template).children)
  }

  toPlainObj() {
    return {
      name: "graph-content-pattern",
      resources: {
        patternData: this.patternData,
      },
    }
  }

  init({ patternData }) {
    this.patternData = patternData
  }
}

customElements.define("graph-content-pattern", GraphContentPattern)
export default GraphContentPattern
